.home-container {
	max-width: 1200px;
	margin: 0 auto;
	padding: 2rem;
}

section {
	margin-bottom: 4rem;
}

h2 {
	font-size: 2rem;
	margin-bottom: 1.5rem;
	color: #2c3e50;
	position: relative;
	padding-bottom: 0.5rem;
	text-align: left;
}

h2::after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 50px;
	height: 3px;
	background-color: #3498db;
}

/* Intro Preview */
.intro-preview {
	text-align: left;
	margin-bottom: 3rem;
}

.intro-preview p {
	font-size: 1.2rem;
	line-height: 1.6;
	color: #34495e;
	margin-bottom: 1rem;
}

.read-more,
.see-all,
.read-post {
	color: #3498db;
	text-decoration: none;
	font-weight: 500;
	display: inline-block;
	transition: color 0.3s;
}

.read-more:hover,
.see-all:hover,
.read-post:hover {
	color: #2980b9;
}

/* Projects Section */
.project-cards {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
	gap: 2rem;
	margin-bottom: 1.5rem;
}

.project-card {
	border-radius: 8px;
	overflow: hidden;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
	transition: transform 0.3s, box-shadow 0.3s;
}

.project-card:hover {
	transform: translateY(-5px);
	box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.project-image img {
	width: 100%;
	height: 200px;
	object-fit: contain;
}

.project-info {
	padding: 1.5rem;
	text-align: left;
}

.project-info h3 {
	margin-bottom: 0.5rem;
	color: #2c3e50;
}

.project-info p {
	color: #7f8c8d;
	margin-bottom: 1rem;
}

.view-button {
	padding: 0.5rem 1rem;
	background-color: #3498db;
	color: white;
	border: none;
	border-radius: 4px;
	cursor: pointer;
	transition: background-color 0.3s;
	text-decoration: none;
	display: inline-block;
}

.view-button:hover {
	background-color: #2980b9;
}

.see-all {
	display: block;
	text-align: right;
	margin-top: 1rem;
}

/* Latest Posts */
.latest-posts {
	text-align: left;
}

.post-preview {
	padding: 2rem;
	background-color: #f8f9fa;
	border-radius: 8px;
	margin-bottom: 1rem;
}

.post-preview h3 {
	color: #2c3e50;
	margin-bottom: 0.5rem;
}

.post-preview p {
	margin-bottom: 1rem;
	color: #5d6d7e;
}

@media (max-width: 768px) {
	.project-cards {
		grid-template-columns: 1fr;
	}
}

/* 다크 모드 스타일 */
.home-container.dark {
	background-color: #121212;
	color: #e0e0e0;
}

.home-container.dark h2,
.home-container.dark h3 {
	color: #ffffff;
}

.home-container.dark .intro-preview p,
.home-container.dark .project-info p {
	color: #b0b0b0;
}

.home-container.dark .project-card {
	background-color: #1e1e1e;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.home-container.dark .post-preview {
	background-color: #1e1e1e;
}

.home-container.dark .read-more,
.home-container.dark .see-all,
.home-container.dark .read-post {
	color: #61dafb;
}

/* 필터 버튼 스타일 */
.project-filters {
	display: flex;
	justify-content: center;
	gap: 1rem;
	margin-bottom: 2rem;
}

.project-filters button {
	padding: 0.5rem 1.5rem;
	background: none;
	border: 1px solid #e0e0e0;
	border-radius: 30px;
	cursor: pointer;
	transition: all 0.3s;
}

.project-filters button.active {
	background-color: #3498db;
	color: white;
	border-color: #3498db;
}

.home-container.dark .project-filters button {
	border-color: #444;
	color: #e0e0e0;
}

.home-container.dark .project-filters button.active {
	background-color: #3498db;
	color: white;
}

/* 테마 전환 버튼 */
.theme-toggle {
	text-align: right;
	margin-bottom: 1rem;
}

.theme-toggle button {
	background-color: #f0f0f0;
	border: none;
	padding: 0.5rem 1rem;
	border-radius: 4px;
	cursor: pointer;
	transition: all 0.3s;
}

.home-container.dark .theme-toggle button {
	background-color: #333;
	color: #e0e0e0;
}

/* 로딩 메시지 */
.loading-message {
	text-align: center;
	padding: 2rem;
	font-style: italic;
	color: #7f8c8d;
}

.home-container.dark .loading-message {
	color: #b0b0b0;
}


